<template>
  <div>
    <table>
      <caption><b>评价量规</b></caption>
      <thead>
        <tr>
          <th>评价维度</th>
          <th>自评</th>
          <th>第2组</th>
          <th>第3组</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="dimension in dimensions" :key="dimension.id">
          <td>{{ dimension.name }}</td>
          <td>
            <select v-model="dimension.self1">
              <option :value="1">⭐️</option>
              <option :value="2">⭐️⭐️</option>
              <option :value="3">⭐️⭐️⭐️</option>
            </select>
          </td>
          <td>
            <select v-model="dimension.group2">
              <option :value="1">⭐️</option>
              <option :value="2">⭐️⭐️</option>
              <option :value="3">⭐️⭐️⭐️</option>
            </select>
          </td>
          <td>
            <select v-model="dimension.group3">
              <option :value="1">⭐️</option>
              <option :value="2">⭐️⭐️</option>
              <option :value="3">⭐️⭐️⭐️</option>
            </select>
          </td>
        </tr>
      </tbody>
    </table>
    <el-row class="button-row">
     <el-button type="primary"   @click="saveData">已完成</el-button>
    </el-row>
  </div>
</template>


<script>
import axios from 'axios'
import { mapMutations } from 'vuex';

export default {
 name: 'GroupOne',

 data() {
   return {
    dimensions: [
      { id: '001', name: '合作度', self1: 0, group2: 0, group3: 0 },
      { id: '002', name: '完成度', self1: 0, group2: 0, group3: 0 },
      { id: '003', name: '美观性', self1: 0, group2: 0, group3: 0 },
      { id: '004', name: '创意性', self1: 0, group2: 0, group3: 0 },
    ]
  }
  },
  methods: {
    ...mapMutations(["updateGroupData"]),
    saveData() {  
   
      axios.post('http://localhost:3000/submit', {
      group: 'group1',
      data: this.dimensions
    })
    .then(() => {
      this.updateGroupData({ group: "group1", data: this.dimensions });
      this.$router.push('/huizong');
    })
    .catch((error) => {
      console.error(error);
    });
      },  
  },
}
</script>

