import Vue from "vue";

import Vuex from 'vuex'

Vue.use(Vuex)


const actions ={
  
}

const mutations = {
  updateGroupData(state, { group, data }) {
    state.category[group] = data;
  },
}

const state = {
  category: {
    group1: [], // 用于存储 group1 路由的数据
    group2: [], // 用于存储 group2 路由的数据
    group3: []  // 用于存储 group3 路由的数据
  },
}



export default new Vuex.Store({
  actions,
  mutations,
  state,
})