    <table>
      <caption>维度他评平均分</caption>
      <thead>
        <tr>
          <th>评价维度</th>
          <th>组1 他评得分</th>
          <th>组2 他评得分</th>
          <th>组3 他评得分</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="dimension in mergedData" :key="dimension.id">
          <td>{{ dimension.name }}</td>
          <td>{{ dimension.otherAverages.group1 +'🌟' || '未评' }}</td>
          <td>{{ dimension.otherAverages.group2 +'🌟'|| '未评' }}</td>
          <td>{{ dimension.otherAverages.group3 +'🌟'|| '未评' }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from "vuex";

export default {
  name: "HuiZong",
  data() {
    return {
      evaluations: null
    };
  },
  computed: {
    ...mapState({
      group1Data: (state) => state.category.group1,
      group2Data: (state) => state.category.group2,
      group3Data: (state) => state.category.group3,
    }),
    mergedData() {
        const keys = ["001", "002", "003", "004"]; // 假设每个评价维度的 ID 是固定的
        return keys.map((id) => {
          const group1 = this.group1Data.find((d) => d.id === id) || {};
          const group2 = this.group2Data.find((d) => d.id === id) || {};
          const group3 = this.group3Data.find((d) => d.id === id) || {};

          // 计算每个小组的他评平均分
          const group1OtherAverage =
            [group2.group1, group3.group1]
              .filter((score) => score !== undefined && score !== null) // 过滤无效数据
              .reduce((sum, score) => sum + score, 0) /
            [group2.group1, group3.group1].filter((score) => score !== undefined && score !== null).length || "未评";

          const group2OtherAverage =
            [group1.group2, group3.group2]
              .filter((score) => score !== undefined && score !== null)
              .reduce((sum, score) => sum + score, 0) /
            [group1.group2, group3.group2].filter((score) => score !== undefined && score !== null).length || "未评";

          const group3OtherAverage =
            [group1.group3, group2.group3]
              .filter((score) => score !== undefined && score !== null)
              .reduce((sum, score) => sum + score, 0) /
            [group1.group3, group2.group3].filter((score) => score !== undefined && score !== null).length || "未评";

          return {
            id,
            name: group1.name || "未知维度",
            group1,
            group2,
            group3,
            otherAverages: {
              group1: group1OtherAverage,
              group2: group2OtherAverage,
              group3: group3OtherAverage,
            },
          };
  });
    },


  },
  mounted() {
  axios.get('http://localhost:3000/summary')
    .then((response) => {
      this.evaluations = response.data;
    })
    .catch((error) => {
      console.error(error);
    });
}
};
</script>

